* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.projects {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background: #f0f0f0;
  padding: 10px 0;
}

.projects-head {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coding {
  width: 100%;
  margin-bottom: 10%;
}

.containerr {
  width: 100%;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 5%;
  margin-top: 5%;
}

.containerr .cardd {
  position: relative;
  min-width: 300px;
  width: 400px;
  height: 270px;
  margin: 3%;
  box-shadow: 5px 5px 10px 1px black;
  border-radius: 20px;
}

.containerr .cardd .face {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.5s;
  border-radius: 20px;
}

.containerr .cardd .face.face1 {
  background: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  transform-origin: bottom;
  border: 1px solid white;
  border-radius: 20px;
}

.containerr .cardd .face.face1 .contentt {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.button {
  border: none;
  padding: 7px 30px;
  cursor: pointer;
  outline: none;
  background: #333;
  color: white;
  font-size: 18px;
  border-radius: 2px;
}

.buttons {
  width: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 5%;
}

.github-btn {
  transition: 0.5s;
  cursor: pointer;
}

.github-btn:hover {
  transform: rotate(360deg);
}

.containerr .cardd .face.face1 img {
  width: 100%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.containerr .cardd .face.face1 h3 {
  margin: 10px 0 0;
  color: #fff;
  text-align: center;
  font-size: 1.5em;
}

.containerr .cardd:hover .face.face1 {
  transform: translateY(-100%) rotateX(90deg);
  background: #1e1e1e;
}

.containerr .cardd .face.face2 {
  border-radius: 20px;
  background: #c07eb1;
  display: flex;
  justify-content: c;
  align-items: center;
  padding: 20px;
  transform-origin: top;
  transform: translateY(100%) rotateX(90deg);
  border: 1px solid white;
}

.containerr .cardd .face.face2 .contentt {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.containerr .cardd:hover .face.face2 {
  transform: translateY(0) rotateX(0deg);
}

.up-wrapper {
  margin-top: 5%;
}

.scroll-to-top {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background: #c07eb1;
  color: white;
  margin-bottom: 10px;
  z-index: 1000;
  cursor: pointer;
  transition: 0.4s;
}

.scroll-to-top:hover {
  background-color: cadetblue;
}

.scroll-to-top i {
  z-index: 1;
  margin-top: 10px;
  animation: scrollTop 0.4s alternate ease infinite;
}

@keyframes scrollTop {
  from {
    transform: translateY(2px);
  }
  to {
    transform: translateY(-1px);
  }
}

@media screen and(max-width: 480px) {
  .containerr .cardd {
    height: 210px;
    margin: 15% 0;
  }
  .containerr .cardd .face.face2 .contentt h4 {
    font-size: 1.2rem;
  }
  .projects2 {
    padding-top: 10%;
  }
}


@media screen and(min-width: 300px) and(max-width: 410px) {
  .containerr .cardd {
    width: 80%;
  }
}

@media screen and(min-width: 410px) and(max-width: 480px) {
  .containerr .cardd {
    width: 70%;
  }
}
