h2 {
  color: #c07eb1;
}

.padding {
  padding: 10px 20%;
}

.about-text {
  font-size: 2rem;
}

.about-title {
  width: -moz-fit-content;
  margin-left: auto;
  margin-right: auto;
}

.front h6,
.back h6,
.tools h6 {
  font-size: 20px;
}

@media screen and(max-width: 480px) {
  h2 {
    color: #c07eb1;
    border-bottom: 1px solid #ddd;
  }
  h3 {
    font-size: 1.3rem;
  }
  .padding {
    padding: 15% 5%;
  }
  .about-text {
    font-size: 1.2rem;
  }
}

@media screen and(min-width: 1500px) {
  .about > h1 {
    /* margin-bottom: 5% !important; */
  }
  .ticker-wrap {
    margin: 5% 0;
  }
}

@media screen and(min-width: 2400px) {
  h1 {
    font-size: 5em;
  }
  h2 {
    font-size: 3em;
  }
  h4 {
    font-size: 2.5em;
  }
  h6 {
    font-size: 2em;
  }
}

@media only screen and(min-width: 1024px) and(max-height: 1366px) and(-webkit-min-device-pixel-ratio: 1.5) {
  .about-title {
    font-size: 4rem;
  }
  .about-text {
    font-size: 1.7rem;
  }
  .back h6,
  .front h6,
  .tools h6 {
    font-size: 1.3rem;
  }
}

